import { css } from "styled-components";

const transitionProps = (
  properties = ["color", "background-color", "border-color"]
) => "".concat(properties.map((property) => `${property} .2s ease `));

export const baseStyles = (propertyList) => css`
  box-sizing: border-box;
  -o-transition: ${transitionProps(propertyList)};
  -moz-transition: ${transitionProps(propertyList)};
  -ms-transition: ${transitionProps(propertyList)};
  -webkit-transition: ${transitionProps(propertyList)};
  transition: ${transitionProps(propertyList)};
`;
